
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapGetters } from 'vuex'

export default {
    components: {
        Scroller
    },
    created(){
        this.$store.dispatch('get_article_category_list')
    },
    data() {
        return {
            
        }
    },
    computed: {
        calWidth() {
            return this.articlecategory.length * 4;
        },
        ...mapGetters([
            'articlecategory'
        ])
    }
}




